import React, { Component } from 'react';
import {
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter
} from 'reactstrap';
import websites from "./website-design";

class Offers extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			websites: websites,
			modalHead: '',
			modalImage: '',
            modalBody: '',
            modalUrl: '',
			show: false
		}
		this.showModal = this.showModal.bind(this);
		this.hideModal = this.hideModal.bind(this);
	}

	showModal(title, image, body, siteUrl) {
		this.setState({
			modalHead: title,
			modalImage: image,
            modalBody: body,
            modalUrl: siteUrl,
			show: true
		});
	}

	hideModal() {
		this.setState({
			show: false
		})
	}

	render() {
		return (
			<div>
				{this.state.websites.map((item, index) => (
                    <div className="gallery-img" key={index}>
                        <a onClick={() => this.showModal(item.name, item.src, item.caption, item.siteUrl)}>
                            <img src={item.thumbnail} width="100%" />
                        </a>
                    </div>
				))}
				<Modal isOpen={this.state.show} toggle={this.hideModal}>
					<ModalHeader toggle={this.hideModal} className="bg-primary">
						<h5 className="display-4 text-white">
							<img src="https://galacticblue.net/img/gb-logo-3-dark.png" height="32" />
							{this.state.modalHead}
						</h5>
					</ModalHeader>
					<ModalBody>
						<img src={this.state.modalImage} width="100%" />
						<p className="text-primary display-5">{this.state.modalBody}</p>
					</ModalBody>
					<ModalFooter>
						{this.state.modalUrl &&
                            <a href={this.state.modalUrl} className="btn-primary px-1 py-1 display-5" target="_blank">Visit the Site</a>
                        }
					</ModalFooter>
				</Modal>
			</div>
		);
	}
}

export default Offers;
